
.PesonalForm{

width: 50%;
margin: 0 auto;
min-height: 100vh;
}
.inputContainer{
     top:100px;
    position: relative;
    text-align: justify;
    width:80%;
    margin-left: 20px;
    justify-items: flex-start;
}
label{
    color: #000000;
    margin-left: 90px;
    position: relative;
    top:1rem
  
}
.emailTxt{
    color:  #4E598C;
    margin-left: 90px;
    
    top:1rem ;
    font-size: 10px;
}

input{
    width: 60%;
    height: 1.5em;
    border-radius: 5px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding:5px 10px;
}
input[type=text]{
    color: #4E598C;
    border: 1px solid #4E598C;
    border-radius: 10px;
    width: 65%;
   
}
input[type=text]:focus{
    color: #4E598C;
    border: 1px solid #4E598C;
    border: 1px solid rgb(212, 206, 206);
}

input[type=Local]{
    color: #4E598C;
    border: 1px solid #4E598C;
    border: 1px solid#4E598C;
    width: 80%;
    margin-left: 80px;

}

Label{
    font-weight: bold;
}

#states{
    color:red;
    text-align: right;
}
.Aside {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    
    
}

select{
    width: 40%;
    margin-left: 70px;
    color: #4E598C;
    border: 1px solid #4E598C;
    border-radius: 10px;
}

.star{
    color: red;
}
@media(max-width: 650px) {

    .PesonalForm{

        width: 100%!important;
      
        }
        label{
            color: #000000;
            margin-left: 60px;
            position: relative;
            top:1rem;
            font-size: 12px;
          
        }
        .emailTxt{
            color:  #4E598C;
            margin-left: 60px;
            
            top:1rem ;
            font-size: 10px;
        }
        input[type=Local]{
            color: #4E598C;
            border: 1px solid #4E598C;
            border: 1px solid#4E598C;
            width: 80%;
            margin-left: 60px;
        
        }
}