
.nav-links{
    display: grid;
    grid-template-columns: repeat(3,auto);
     
    justify-content: center;
    grid-gap: 2.5rem;
    margin-left: 15px;
    list-style-type: none;
    width: 70%;
    height: 30px;
     border-bottom: 1px solid #F2994A;
}

 .nav-link {
    text-decoration: none;
    transition: 0.3s all;
    list-style-type: none;


}
.nav-linkActive{
  
    display: block;
    text-decoration: none!important;
    list-style-type: none;   
    border-radius: 10px;
    text-decoration: none;
    color: #F2994A;
    border-bottom: 1px solid #F2994A;
    top: 0;
    bottom: 0;
    position: relative;
    
    
    }
    .nav-linkActive:before{
  
       content: "";
       top: 0;
       max-width: 50px;
       position: absolute;
    
        
        
        }
.nav-linkActive::after{
content: "";
display: block;
width: 55%;
list-style-type: none;
height:6px;
position: relative;
top: 7px;
bottom: 0;
left: 1.5rem;
border-radius: 10px;
text-decoration: none;
color: #F2994A;
background-color: #F2994A;
transform: scaleX(2);
transition: 0.3s ease;

}


.compTxt{
    text-align: center;
    color:#4E598C;
}
.MainCompleteCont{
    top: 100px;
    position: relative;
}

@media(max-width: 650px) {

   
    .nav-links{
        font-size: 12px;
        width: 100%;
        margin: 0 auto;
     
    }
  
 
             
  
        
            }