.BillingContainer{
   
    width: 50%;
    color: #4E598C;
    margin: 0 auto;
    min-height: 50vh;
    
    
}
.table_container{
  
    top:100px;
    position: relative;
    text-align: justify;
    width:80%;
    margin: 0 auto;
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(33, 30, 67, 0.08);
    border-radius: 10px;
    
}


.pricce{
    margin-left: 9rem;
}
.itemTxt{
    padding-left: 30px;
}
td{
    
    padding-left: 60px; 
    padding-top: 3rem;
   
   
}

.table{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
    position: relative;
    
    padding: 2px;
   
    top: 1px;
    border: 2px solid #2F80ED;;
    border-radius: 10px 10px 0px 0px;
    width: 98%;
    background-color: #2F80ED;
    color: #ffffff;
  
}
.prixtxt{
    margin-left: 22.5rem;
    position: absolute;

}
.TotalCont{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin-left: 2rem;
    padding-left: 10rem;
    position: relative;

   width: 17rem;
   border: 1px solid gray;
   margin-left: 3rem;
   padding-left: 10rem;
   position: relative;
   top: 4rem;
   
}
.underline{
    width: 80%;
    position:absolute;
    top: 150px;
    border-bottom: 1px solid gray
    
}
.totalprix{
    position: relative;
    margin-left: 40px;
}
.totalTxt{
     
    margin-left: -140px;
}
.bill_container{
    width: 100%;
    height: 3em;
    border-radius: 5px;
    display: block;
    position: relative;
    margin-left: 2rem;
    padding:15px 15px;
    top: 7rem;
}
.btn{
    width: 25%;
    height: 2rem;
    border-radius: 8px;
    border:none;
    color:#F7F7FF;
    font-weight: bold;
    background: linear-gradient(180deg, #F2C94C 0%, #F2994A 100%);


}
.btn1{
    width: 25%;
    height: 2rem;
    border-radius: 8px;
    border:none;
    color:#4E598C;
    background: #ffffff;
    font-weight: bold;
    box-shadow: 0px 6px 10px rgba(33, 30, 67, 0.08);
}
@media(max-width: 650px) {

    .BillingContainer{
   
        width: 100%;
       
        
        
    }
   
}