
.MainContainer{
    width: 50%;
    color: #4E598C;
    margin: 0 auto;
    min-height: 50vh;
    
    position: relative;
    justify-content: flex-start;
}
.purchaseContainer{
   
    margin: 0 auto;
    width: 60%;
    padding-left: 30px;
    padding-bottom: 30px;
    box-shadow: 0px 6px 10px rgba(33, 30, 67, 0.08);
    border-radius: 10px;
    background: #FFFFFF;
    position: relative;
    top: 9rem;
}
.good{
    background-color: #ffffff;
    margin-left: 10rem;
    text-align: center;
    margin-top: 10px;
}
.person{
    color: #F2994A;
}
@media(max-width: 650px) {

   
.MainContainer{
    width: 100%;
   
}
.good{
    background-color: #ffffff;
   margin-left: 5em;
}
   
}